import * as React from 'react';
import { HelpTheatreType } from '../../interfaces';
import { HelpTheatreCoordinate } from './coordinate';
import { HelpTheatreSize } from './size';
import { HelpTheatrePivot } from './pivot';
import { HelpTheatreAction } from './action';

interface IProps {
    cur_type:HelpTheatreType;
}

export class HelpTheatreViews extends React.PureComponent<IProps, any> {

    private get_sort_types = () => {
      const { cur_type } = this.props;
      const default_sort = [HelpTheatreType.Coordinate, HelpTheatreType.Size, HelpTheatreType.Pivot, HelpTheatreType.Action];
      const index = default_sort.findIndex((type:HelpTheatreType) => cur_type === type);
      if (index > 0) {
        default_sort.splice(index, 1);
        default_sort.unshift(cur_type);
      }
      return default_sort;
    }

    private render_view_by_type = (type:HelpTheatreType) => {
      const { cur_type } = this.props;
      switch (type) {
        case HelpTheatreType.Coordinate:
          return (
            <HelpTheatreCoordinate
              key={type}
              first={cur_type === HelpTheatreType.Coordinate} />
          );
        case HelpTheatreType.Size:
          return (
            <HelpTheatreSize
              key={type}
              first={cur_type === HelpTheatreType.Size} />
          );
        case HelpTheatreType.Pivot:
          return (
            <HelpTheatrePivot
              key={type}
              first={cur_type === HelpTheatreType.Pivot} />
          );
        case HelpTheatreType.Action:
          return (
            <HelpTheatreAction
              key={type}
              first={cur_type === HelpTheatreType.Action} />
          );
        default:
          return null;
      }
    }

    public render() {
      const sort_types = this.get_sort_types();
      return (
        <React.Fragment>
          {
            sort_types.map((type:HelpTheatreType) => this.render_view_by_type(type))
          }
        </React.Fragment>
      );
    }
}
