import * as React from 'react';
import { get_touch_xy, get_adapt_class_name, get_pixel } from '../../utils';
import './style.scss';

interface IProps {
    visible:boolean;
    real_size:number;
    container_size:number;
    position:number;
    on_move:(y:number, x:number) => void;
    on_focus:() => void;
    container_style?:React.CSSProperties;
}

interface IState {
    position:number;
}

const default_scroll_size = 20;

export class ScrollBar extends React.PureComponent<IProps, IState> {
    private scrollbar:HTMLDivElement | null = null;
    private is_dragging:boolean = false;
    private last_position:number = 0;

    constructor(props:IProps) {
      super(props);
      const new_position = this.calculate_position(props);
      this.state = {
        position: new_position,
      };
    }

    public componentDidUpdate(prevProps:IProps) {
      if (prevProps.real_size !== this.props.real_size ||
            prevProps.container_size !== this.props.container_size ||
            prevProps.position !== this.props.position) {
        const new_position = this.calculate_position(this.props);
        this.setState({ position: new_position });
      }
    }

    private set_bar_ref = (ref:any) => this.scrollbar = ref;

    private calculate_fractional_position(realContentSize:number, containerSize:number, contentPosition:number) {
      if (!realContentSize || !containerSize || !contentPosition) { return 0; }
      const relative_size = realContentSize - containerSize;
      return 1 - ((relative_size - contentPosition) / relative_size);
    }

    private calculate_position(props:IProps) {
      const fractional_position = this.calculate_fractional_position(props.real_size, props.container_size, props.position);
      const scroll_size = this.scrollbar ? this.scrollbar.offsetHeight : default_scroll_size;

      const offset = this.get_offset_from_container_style();

      const scroll_position = (props.container_size - offset - scroll_size) * fractional_position;
      return Math.round(scroll_position);
    }

    private get_offset_from_container_style():number {
      const { container_style } = this.props;
      const offset = container_style ? get_pixel(container_style.marginTop) + get_pixel(container_style.marginBottom) +
            get_pixel(container_style.paddingTop) + get_pixel(container_style.paddingBottom) : 0;
      return offset;
    }

    private compute_multiplier() {
      const { container_size, real_size } = this.props;
      const offset = this.get_offset_from_container_style();
      return (container_size - offset) / real_size;
    }

    private handle_start = (e:React.MouseEvent | React.TouchEvent) => {
      this.is_dragging = true;
      this.last_position = get_touch_xy(e).y;
      this.props.on_focus();
      e.stopPropagation();
    }

    private handle_move = (e:React.MouseEvent | React.TouchEvent) => {
      if (this.is_dragging) {
        const multiplier = this.compute_multiplier();
        const y = get_touch_xy(e).y;
        const delta_y = this.last_position - y;
        this.last_position = y;
        this.props.on_move(delta_y / multiplier, 0);
      }
      e.stopPropagation();
    }

    private handle_end = (e:React.MouseEvent | React.TouchEvent) => {
      this.is_dragging = false;
      e.stopPropagation();
    }

    public render() {
      const { visible, container_style } = this.props;
      const style = {
        marginTop: this.state.position,
      } as React.CSSProperties;
      return (
        <div
          className={get_adapt_class_name('scrollbar_container')}
          style={container_style}
        >
          <div
            className={`scrollbar ${visible ? 'show' : 'hide'}`}
            ref={this.set_bar_ref}
            style={style}
            onTouchStart={this.handle_start}
            onTouchMove={this.handle_move}
            onTouchEnd={this.handle_end}
            onMouseDown={this.handle_start}
            onMouseMove={this.handle_move}
            onMouseUp={this.handle_end}
          />
        </div>
      );
    }
}
