import * as React from 'react';
import { get_adapt_class_name } from '../../../utils';
import './style.scss';

interface IProps {
  major:string;
  minor:string;
  className?:string;
}

export const HelpTitle:React.FC<IProps> = (props:IProps) => {
  const { major, minor, className } = props;
  return (
    <div className={`${get_adapt_class_name('help_title')} ${className || ''}`}>
      <div className={get_adapt_class_name('major_title')}>{major}</div>
      <div className={get_adapt_class_name('minor_title')}>{minor}</div>
    </div>
  );
};
