import * as React from 'react';
import { HelpEffectsType } from '../../interfaces';
import { HelpEffectsBrightness } from './brightness';
import { HelpEffectsColors } from './color_effects';
import { HelpEffectsPixelated } from './pixelated';
import { HelpEffectsOthers } from './others';
import { HelpEffectsDistortion } from './distortion';
import { HelpEffectsRipple } from './ripple';
import { HelpEffectsAlpha } from './alpha';
import { HelpEffectsBlackWhite } from './black_white';

interface IProps {
    cur_type:HelpEffectsType;
}

export class HelpEffectsViews extends React.PureComponent<IProps, any> {

    private get_sort_types = () => {
      const { cur_type } = this.props;
      const default_sort = [HelpEffectsType.Others, HelpEffectsType.ColorEffects, HelpEffectsType.Alpha, HelpEffectsType.Brightness, HelpEffectsType.Pixelated,
        HelpEffectsType.Ripple, HelpEffectsType.Distortion, HelpEffectsType.BlackWhite];
      const index = default_sort.findIndex((type:HelpEffectsType) => cur_type === type);
      if (index > 0) {
        default_sort.splice(index, 1);
        default_sort.unshift(cur_type);
      }
      return default_sort;
    }

    private render_view_by_type = (type:HelpEffectsType) => {
      const { cur_type } = this.props;
      switch (type) {
        case HelpEffectsType.Brightness:
          return (
            <HelpEffectsBrightness
              key={type}
              first={cur_type === HelpEffectsType.Brightness} />
          );
        case HelpEffectsType.ColorEffects:
          return (
            <HelpEffectsColors
              key={type}
              first={cur_type === HelpEffectsType.ColorEffects} />
          );
        case HelpEffectsType.Pixelated:
          return (
            <HelpEffectsPixelated
              key={type}
              first={cur_type === HelpEffectsType.Pixelated} />
          );
        case HelpEffectsType.Others:
          return (
            <HelpEffectsOthers
              key={type}
              first={cur_type === HelpEffectsType.Others} />
          );
        case HelpEffectsType.Distortion:
          return (
            <HelpEffectsDistortion
              key={type}
              first={cur_type === HelpEffectsType.Distortion} />
          );
        case HelpEffectsType.Ripple:
          return (
            <HelpEffectsRipple
              key={type}
              first={cur_type === HelpEffectsType.Ripple} />
          );
        case HelpEffectsType.Alpha:
          return (
            <HelpEffectsAlpha
              key={type}
              first={cur_type === HelpEffectsType.Alpha} />
          );
        case HelpEffectsType.BlackWhite:
          return (
            <HelpEffectsBlackWhite
              key={type}
              first={cur_type === HelpEffectsType.BlackWhite} />
          );
        default:
          return null;
      }
    }

    public render() {
      const sort_types = this.get_sort_types();
      return (
        <React.Fragment>
          {
            sort_types.map((type:HelpEffectsType) => this.render_view_by_type(type))
          }
        </React.Fragment>
      );
    }
}
