import * as React from 'react';

interface IProps {
  title:string;
  src?:string;
  on_play?(play:boolean):void;
}

interface IState {
  playing:boolean;
}

export class MusicDemo extends React.Component<IProps, IState> {
  private audio?:HTMLAudioElement;
  private rAF?:number;

  constructor(props:IProps) {
    super(props);
    this.state = {
      playing: false,
    };
  }

  private update_canvas = () => {
    if (!this.audio) { return; }
    const canvas = document.querySelector<HTMLCanvasElement>('#music_play');
    if (!canvas) { return; }
    const ctx = canvas.getContext('2d');
    if (!ctx) { return; }

    ctx.clearRect(0, 0, 52, 52);
    const ratio = this.audio.currentTime / this.audio.duration;
    const process = (2 * ratio - 0.5) * Math.PI;
    ctx.beginPath();
    ctx.arc(26, 26, 24, -Math.PI / 2, process);
    ctx.lineWidth = 2;
    ctx.strokeStyle = 'rgba(255, 255, 255, 0.8)';
    ctx.stroke();

    ctx.arc(26, 26, 24, process, 3 * Math.PI / 2);
    ctx.lineWidth = 2;
    ctx.strokeStyle = 'rgba(255, 255, 255, 0.2)';
    ctx.stroke();

    if (this.audio.ended) {
      this.rAF && cancelAnimationFrame(this.rAF);
      this.on_play(false);
      return;
    }

    this.rAF = requestAnimationFrame(this.update_canvas);
  }

  public play = (should_start:boolean) => {
    if (!this.audio) { return; }
    this.on_play(should_start);
    if (should_start) {
      this.audio.currentTime = 0;
      this.audio.play();
      this.rAF = requestAnimationFrame(this.update_canvas);
    } else {
      this.audio.pause();
      this.rAF && cancelAnimationFrame(this.rAF);
    }
  }

  private on_play = (start:boolean) => {
    this.setState({ playing: start });
    this.props.on_play && this.props.on_play(start);
  }

  public render() {
    return (
      <div className='swiper-slide'>
        {!this.state.playing ? (
          <div
            className='music-play-button'
            onClick={this.play.bind(this, true)}>
            <div className='breathing' />
          </div>
        ) : (
          <div
            className='music-stop-button'
            onClick={this.play.bind(this, false)}>
            <canvas
              id='music_play'
              width={52}
              height={52} />
            <div className='stop-button-icon' />
          </div>
        )}
        <div className='music-title'>{this.props.title}</div>
        <audio
          ref={(ref) => { if (ref) { this.audio = ref; }}}
          autoPlay={false}
          src={this.props.src}
          style={{ visibility: 'hidden' }} />
      </div>
    );
  }
}
