import * as React from 'react';
import { HelpParagraphs, HelpInfoMeta } from '../../../interfaces';
import { get_adapt_class_name } from '../../../utils';
import './style.scss';

interface IProps extends HelpParagraphs {
  type?:'subtitle' | 'item' | 'subitem';
  style?:React.CSSProperties;
}

export class HelpParagraph extends React.PureComponent<IProps, any> {
  public render() {
    const { infos, type = 'item', style = {} } = this.props;
    return (
      <div
        className={`${get_adapt_class_name('help_paragraph')} ${type}_style`}
        style={style}>
        {
          infos && infos.map((info:HelpInfoMeta) => (
            <span
              key={info.info + info.type}
              className={`${info.type}_paragraph`}>
              {info.info}
            </span>
          ))
        }
      </div>
    );
  }
}
