import * as React from 'react';
import * as ReactDom from 'react-dom';
import './polyfill';
import './intersection_observer';
import { HelpRoot } from './help';
import { MicrobitHelp } from './microbit/help';
import { get_cur_type } from './utils';


function init() {
  const help_root = document.getElementById('help_root') as HTMLElement;
  const type = get_cur_type();
  if (type === 'microbit') {
    ReactDom.render(<MicrobitHelp />, help_root);

  } else {
    ReactDom.render(<HelpRoot />, help_root);
  }
}

init();
