import * as React from 'react';
import { HelpPenType } from '../../interfaces';
import { HelpPenBrightness } from './brightness';
import { HelpPenSaturation } from './saturation';
import { HelpPenColors } from './colors';

interface IProps {
    cur_type:HelpPenType;
}

export class HelpPenViews extends React.PureComponent<IProps, any> {

    private get_sort_types = () => {
      const { cur_type } = this.props;
      const default_sort = [HelpPenType.Colors, HelpPenType.Saturation, HelpPenType.Brightness];
      const index = default_sort.findIndex((type:HelpPenType) => cur_type === type);
      if (index > 0) {
        default_sort.splice(index, 1);
        default_sort.unshift(cur_type);
      }
      return default_sort;
    }

    private render_view_by_type = (type:HelpPenType) => {
      const { cur_type } = this.props;
      switch (type) {
        case HelpPenType.Brightness:
          return (
            <HelpPenBrightness
              key={type}
              first={cur_type === HelpPenType.Brightness} />
          );
        case HelpPenType.Saturation:
          return (
            <HelpPenSaturation
              key={type}
              first={cur_type === HelpPenType.Saturation} />
          );
        case HelpPenType.Colors:
          return (
            <HelpPenColors
              key={type}
              first={cur_type === HelpPenType.Colors} />
          );
        default:
          return null;
      }
    }

    public render() {
      const sort_types = this.get_sort_types();
      return (
        <React.Fragment>
          {
            sort_types.map((type:HelpPenType) => this.render_view_by_type(type))
          }
        </React.Fragment>
      );
    }
}
