import * as React from 'react';
import Swiper, { SwiperOptions } from 'swiper';

import { MusicDemo } from './music';

import '../../../../../../node_modules/swiper/dist/css/swiper.min.css';
import './style.scss';

const audio_source = [
  { title: 'MIDI', src: require('../../../../../assets/webview_res/audio1.mp3') },
  { title: 'MIDI+', src: require('../../../../../assets/webview_res/audio2.mp3') },
  { title: 'MIDI', src: require('../../../../../assets/webview_res/audio3.mp3') },
];

export class LoopItems extends React.Component {
  private swiper?:Swiper;
  private ref:HTMLDivElement | null = null;
  private musics:MusicDemo[] = [];
  private intersction_observer:IntersectionObserver | undefined;

  private init_swiper_config() {
    const config:SwiperOptions = {
      slidesPerView: 1,
      centeredSlides: true,
      spaceBetween: 21,
      loop: true,
      autoplay: {
        disableOnInteraction: false,
      },
      pagination: {
        el: '.swiper-pagination',
        type: 'custom',
        renderCustom: function(swiper:Swiper, current:number, total:number) {
          let _html = '<ul class="self_define_pagination">';
          for (let i = 1; i <= total; i++) {
            if (current == i) {
              _html += '<li class="swiper_pagination_custom active"/>';
            } else {
              _html += '<li class="swiper_pagination_custom"/>';
            }
          }
          return `${_html }</ul>`;
        },
      },
      direction: 'horizontal',
      on: {
        slideChange: () => {
          this.musics.forEach((music) => {
            music.play(false);
          });
        },
      },
    };

    return config;
  }

  public componentDidMount() {
    this.swiper = new Swiper('.swiper-container', this.init_swiper_config());
    if (this.ref) {
      this.intersction_observer = new IntersectionObserver(this.stop_all);
      this.intersction_observer && this.intersction_observer.observe(this.ref);
    }
  }

  private stop_all = (entries:IntersectionObserverEntry[]) => {
    if (this.ref && entries[0] && entries[0].intersectionRatio <= 0) {
      this.musics.forEach((music) => music.play(false));
    }
  }

  private toggle_autoplay = (start:boolean) => {
    if (this.swiper && this.swiper.autoplay) {
      start ? this.swiper.autoplay.stop() : this.swiper.autoplay.start();
    }
  }

  public render() {
    return (
      <div
        className='swiper-container'
        ref={(ref) => { if (ref) { this.ref = ref; } }}>
        <div className='swiper-wrapper' >
          {audio_source.map((source) => (
            <MusicDemo
              ref={(ref) => { if (ref) { this.musics.push(ref); } }}
              key={source.title}
              src={source.src}
              title={source.title}
              on_play={this.toggle_autoplay} />
          ))}
        </div>
        <div className='swiper-pagination swiper-pagination-timeline-page' />
      </div>
    );
  }
}
