export function get_touch_xy(event:React.TouchEvent | React.MouseEvent) {
  let x = 0;
  let y = 0;
  const touch_event:React.Touch = (event as React.TouchEvent).touches && (event as React.TouchEvent).touches[0];
  if (touch_event) {
    if (touch_event.pageX) {
      x = touch_event.pageX;
      y = touch_event.pageY;
    } else {
      x = touch_event.clientX + document.body.scrollLeft - document.body.clientLeft;
      y = touch_event.clientY + document.body.scrollTop - document.body.clientTop;
    }
  } else {
    if ((event as React.MouseEvent).pageX) {
      x = (event as React.MouseEvent).pageX;
      y = (event as React.MouseEvent).pageY;
    } else {
      x = (event as React.MouseEvent).clientX + document.body.scrollLeft - document.body.clientLeft;
      y = (event as React.MouseEvent).clientY + document.body.scrollTop - document.body.clientTop;
    }
  }
  return {
    x: x,
    y: y,
  };
}

export function get_body_height() {
  const height = document.body.offsetHeight || 343;
  return height;
}

export function is_ipad() {
  const height = document.body.offsetHeight || 0;
  const width = document.body.offsetWidth || 0;
  return height >= 514 && width >= 852;
}

export function get_adapt_class_name(base:string) {
  const ipad = is_ipad();
  if (ipad) {
    return `${base } ipad`;
  }
  return base;
}

export function get_adapt_video_size(width:number, height:number) {
  if (is_ipad()) {
    let new_w = width * 1.5;
    let new_h = height * 1.5;
    if ((new_w & 1) === 1) {
      new_w += 1;
    }
    if ((new_h & 1) === 1) {
      new_h += 1;
    }
    return { width: new_w, height: new_h };
  }
  return { width, height };
}

export function get_values(obj:any) {
  const result = [];
  for (const key in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, key)) {
      result.push(obj[key]);
    }
  }
  return result;
}

export function get_pixel(property:string | number | undefined):number {
  if (typeof property === 'number') {
    return property;
  } else if (typeof property === 'string') {
    return Number(property.replace('px', ''));
  }
  return 0;
}

export function get_cur_type() {
  const reg = new RegExp('(^|&)type=([^&]*)(&|$)', 'i');
  const r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return decodeURIComponent(r[2]);
  }
  return null;
}

export function copyText(text: string) {
  const input = document.createElement('textarea');
  input.setAttribute('readonly', 'readonly');
  input.value = text;
  document.body.appendChild(input);
  input.select();
  input.setSelectionRange(0, 9999);
  const state = document.execCommand('copy');
  document.body.removeChild(input);
  return state;
}

export function showToast(text:string, t:number = 2000) {
  const toast = document.createElement('div');
  toast.style.height = '36px';
  toast.style.lineHeight = '36px';
  toast.style.background = 'rgba(0,0,0,0.76)';
  toast.style.borderRadius = '4px';
  toast.style.padding = '0 16px';
  toast.style.color = '#ffffff';
  toast.style.fontSize = '14px';
  toast.style.position = 'fixed';
  toast.style.left = '50%';
  toast.style.top = '4px';
  toast.style.transform = 'translate(-50%, 0)';

  toast.innerText = text;

  document.body.appendChild(toast);

  setTimeout(() => {
    document.body.removeChild(toast);
  }, t);
}
