import * as React from 'react';
import { HelpSection } from '../../common/section';
import { HelpParagraph } from '../../common/paragraph';
import { HelpWrap } from '../../common/base_wrap';
import { HelpInfoMeta, CommonHelpProps } from '../../../interfaces';
import { get_adapt_class_name } from '../../../utils';
import './style.scss';

const infos_one = [{ info: '', type: 'normal' }, { info: '', type: 'highlight' }] as HelpInfoMeta[];
const infos_two = [{ info: '', type: 'normal' }] as HelpInfoMeta[];
const infos_three = [{ info: 'X', type: 'highlight' }, { info: '', type: 'normal' }, { info: '', type: 'highlight' },
  { info: 'x', type: 'normal' }] as HelpInfoMeta[];
const infos_four = [{ info: 'Y', type: 'highlight' }, { info: '', type: 'normal' }, { info: '', type: 'highlight' },
  { info: 'y', type: 'normal' }] as HelpInfoMeta[];

export class HelpTheatreCoordinate extends React.PureComponent<CommonHelpProps, any> {

  public render() {
    const { first } = this.props;
    return (
      <HelpWrap
        className='help_theatre_coordinate'
        first={first}>
        <img
          className={get_adapt_class_name('help_theatre_coordinate_img')}
          src={require('../../../../assets/webview_res/help_theatre_corrdinate.png')} />
        <HelpSection title=''>
          <HelpParagraph infos={infos_one} />
          <HelpParagraph infos={infos_two} />
          <HelpParagraph infos={infos_three} />
          <HelpParagraph infos={infos_four} />
        </HelpSection>
      </HelpWrap>
    );
  }
}
