import * as React from 'react';
import { get_touch_xy } from '../../utils';
import { ScrollBar } from './bar';

import './style.scss';

type computeSize = {
  real_height:number;
  container_height:number;
  real_width:number;
  container_width:number;
  top_position?:number;
};

type PreviousEventValues = {
  x:number;
  y:number;
  delta_x:number;
  delta_y:number;
  timestamp?:number;
};

interface IProps {
  classname?:string;
  content_classname?:string;
  style?:React.CSSProperties;
  content_style?:React.CSSProperties;
  container_style?:React.CSSProperties;
  stop_scroll_propagation?:boolean;
  on_scroll?:(data:any) => void;
}

interface IState {
  bar_visible:boolean;
  top_position:number;
  real_height:number;
  container_height:number;
  real_width:number;
  container_width:number;
}

function positive_or_zero(v:number) {
  return v < 0 ? 0 : v;
}

export class ScrollView extends React.PureComponent<IProps, IState> {
  private wrapper:HTMLDivElement | null = null;
  private content:HTMLDivElement | null = null;
  private previous_event_values:PreviousEventValues = {
    x: 0,
    y: 0,
    delta_x: 0,
    delta_y: 0,
  };
  private is_dragging:boolean = false;

  constructor(props:IProps) {
    super(props);
    this.state = {
      bar_visible: false,
      top_position: 0,
      real_height: 0,
      container_height: 0,
      real_width: 0,
      container_width: 0,
    };
  }

  public componentDidMount() {
    this.set_sizes_to_state();
    if (this.wrapper) {
      this.wrapper.addEventListener('resize', this.handle_resize);
    }
  }

  public componentDidUpdate() {
    this.set_sizes_to_state();
  }

  public componentWillUnmount() {
    if (this.wrapper) {
      this.wrapper.removeEventListener('resize', this.handle_resize);
    }
  }

  private set_wrapper_ref = (ref:any) => (this.wrapper = ref);

  public set_state_with_cb(new_state:any, need_cb:boolean = true) {
    if (this.props.on_scroll && need_cb) {
      this.props.on_scroll(new_state);
    }
    this.setState({ ...new_state });
  }

  public set_sizes_to_state() {
    const sizes = this.compute_sizes();
    if (sizes.real_height !== this.state.real_height || sizes.real_width !== this.state.real_width) {
      this.set_state_with_cb(this.get_modified_positions_if_needed(sizes), false);
    }
  }

  public get_modified_positions_if_needed(new_size:computeSize) {
    const bottom_position = new_size.real_height - new_size.container_height;
    if (this.state.top_position >= bottom_position) {
      new_size.top_position = this.can_scroll(new_size as IState) ? positive_or_zero(bottom_position) : 0;
    }

    return new_size;
  }

  private get_line_height() {
    if (this.content) {
      const compute_style = getComputedStyle(this.content);
      const line_height = compute_style && compute_style.lineHeight;
      if (line_height && typeof line_height === 'number') {
        return line_height;
      }
    }
    return 1;
  }

  public compose_new_state(delta_x:number, delta_y:number) {
    const new_state = this.compute_sizes() as computeSize;

    if (this.can_scroll(new_state as IState)) {
      new_state.top_position = this.compute_top_position(delta_y, new_state);
    } else {
      new_state.top_position = 0;
    }

    return new_state;
  }

  public compute_top_position(deltaY:number, sizes:computeSize) {
    const new_top_position = this.state.top_position - deltaY;
    return this.normalize_top_position(new_top_position, sizes);
  }

  public normalize_top_position(new_top_position:number, sizes:computeSize) {
    if (new_top_position > sizes.real_height - sizes.container_height) {
      new_top_position = sizes.real_height - sizes.container_height;
    }
    if (new_top_position < 0) {
      new_top_position = 0;
    }
    return new_top_position;
  }

  private compute_sizes():computeSize {
    const real_height = this.content ? this.content.offsetHeight : 0;
    const container_height = this.wrapper ? this.wrapper.offsetHeight : 0;
    const real_width = this.content ? this.content.offsetWidth : 0;
    const container_width = this.wrapper ? this.wrapper.offsetWidth : 0;

    return { real_height, container_height, real_width, container_width };
  }

  public scroll_top() {
    this.scroll_to(0);
  }

  public scroll_bottom() {
    this.scroll_to(this.state.real_height - this.state.container_height);
  }

  public get_top() {
    return this.state.top_position;
  }

  public scroll_to(top_tosition:number) {
    if (this.can_scroll()) {
      const position = this.normalize_top_position(top_tosition, this.compute_sizes());
      this.set_state_with_cb({ top_position: position });
    }
  }

  public can_scroll(state = this.state) {
    const scrollable = state.real_height > state.container_height;
    return scrollable;
  }

  public handle_start = (e:React.TouchEvent | React.MouseEvent) => {
    const { x, y } = get_touch_xy(e);
    this.previous_event_values = {
      ...this.previous_event_values,
      x,
      y,
      timestamp: Date.now(),
    };
    this.is_dragging = true;
    this.show_bar();
  };

  public handle_move = (e:React.TouchEvent | React.MouseEvent) => {
    if (!this.is_dragging) {
      return;
    }
    if (this.can_scroll()) {
      e.stopPropagation();
    }

    const { x, y } = get_touch_xy(e);

    const delta_y = this.previous_event_values.y - y;
    const delta_x = this.previous_event_values.x - x;

    this.previous_event_values = {
      ...this.previous_event_values,
      delta_y,
      delta_x,
      x,
      y,
      timestamp: Date.now(),
    };

    this.set_state_with_cb(this.compose_new_state(-delta_x, -delta_y));
  };

  public handle_end = (e:React.TouchEvent | React.MouseEvent) => {
    let { delta_x, delta_y } = this.previous_event_values;
    if (typeof delta_x === 'undefined') {
      delta_x = 0;
    }
    if (typeof delta_y === 'undefined') {
      delta_y = 0;
    }
    const timestamp = this.previous_event_values.timestamp || Date.now();
    if (Date.now() - timestamp < 200) {
      this.set_state_with_cb(this.compose_new_state(-delta_x * 10, -delta_y * 10));
    }

    this.previous_event_values = {
      ...this.previous_event_values,
      delta_y: 0,
      delta_x: 0,
    };
    this.is_dragging = false;
    this.delay_hide_bar();
  };

  public handle_scrollbar_move = (delta_y:number, delta_x:number) => {
    this.set_state_with_cb(this.compose_new_state(delta_x, delta_y));
  };

  public handle_scrollbar_position_change = (position:number) => {
    this.scroll_to(position);
  };

  public handle_wheel = (e:any) => {
    let delta_y = e.deltaY;
    let delta_x = e.deltaX;

    /*
     * WheelEvent.deltaMode can differ between browsers and must be normalized
     * e.deltaMode === 0: The delta values are specified in pixels
     * e.deltaMode === 1: The delta values are specified in lines
     * https://developer.mozilla.org/en-US/docs/Web/API/WheelEvent/deltaMode
     */
    if (e.deltaMode === 1) {
      delta_y = delta_y * this.get_line_height();
      delta_x = delta_x * this.get_line_height();
    }

    const new_state = this.compose_new_state(-delta_x, -delta_y);

    if (
      (new_state.top_position && this.state.top_position !== new_state.top_position) ||
      this.props.stop_scroll_propagation
    ) {
      e.stopPropagation();
    }

    this.set_state_with_cb(new_state);
    this.on_focus_content();

    if (!this.state.bar_visible) {
      this.show_bar();
      this.delay_hide_bar();
    }
  };

  public handle_resize = () => {
    let new_state = this.compute_sizes();
    new_state = this.get_modified_positions_if_needed(new_state);
    this.set_state_with_cb(new_state, false);
  };

  private show_bar = () => this.setState({ bar_visible: true });

  private delay_hide_bar = () => setTimeout(() => this.setState({ bar_visible: false }), 1000);

  public on_focus_content = () => {
    if (this.content) {
      this.content.focus();
    }
  };

  public render() {
    const { children, classname, content_classname } = this.props;
    const { bar_visible } = this.state;

    const classes = `scrollview ${classname || ''}`;
    const content_classes = `scrollview_content ${content_classname || ''}`;

    const content_style = {
      marginTop: -this.state.top_position,
    };

    return (
      <div
        ref={this.set_wrapper_ref}
        className={classes}
        style={this.props.style}>
        <div
          ref={(x) => (this.content = x)}
          style={{ ...this.props.content_style, ...content_style }}
          className={content_classes}
          onTouchStart={this.handle_start}
          onTouchMove={this.handle_move}
          onTouchEnd={this.handle_end}
          onMouseDown={this.handle_start}
          onMouseMove={this.handle_move}
          onMouseUp={this.handle_end}
          onWheel={this.handle_wheel}
        >
          {children}
        </div>
        <ScrollBar
          visible={bar_visible}
          real_size={this.state.real_height}
          container_size={this.state.container_height}
          position={this.state.top_position}
          container_style={this.props.container_style}
          on_move={this.handle_scrollbar_move}
          on_focus={this.on_focus_content}
        />
      </div>
    );
  }
}
