import * as React from 'react';
import { get_adapt_class_name } from '../../../utils';
import './style.scss';

interface IProps {
    className?:string;
    title?:string;
}

export const HelpSection:React.FC<IProps> = (props) => {
  const { children, title, className } = props;
  let classes = get_adapt_class_name('help_section');
  classes += className ? ` ${className}` : '';
  return (
    <div className={classes}>
      {title && <div className={get_adapt_class_name('help_section_title')}>{title}</div>}
      <div className='help_section_content'>
        {children}
      </div>
    </div>
  );
};
