import * as React from 'react';

import { get_adapt_class_name, is_ipad } from '../../../utils';
import './style.scss';

interface IProps {
  major:string[];
  minor:string[];
  scroll_down?:boolean;
}

export const HelpMidiTitle:React.FC<IProps> = (props:IProps) => {
  const [pad_width, set_pad_width] = React.useState<string>();
  // Hack on some ipad, can't get the correct measurement on first render
  React.useEffect(() => set_pad_width('120%'), []);

  const { major, minor } = props;
  return (
    <div
      className={`${get_adapt_class_name('help_midi_title')}`}
      style={is_ipad() ? { width: pad_width } : { width: '100%' }}>
      <div className={`title_1 ${props.scroll_down ? 'title1_scroll' : ''}`}>
        <div className={get_adapt_class_name('major_title')}>{major[0]}</div>
        <div className={get_adapt_class_name('minor_title')}>{minor[0]}</div>
      </div>
      <div className={`title_2 ${props.scroll_down ? '' : 'title2_scroll'}`}>
        <div className={get_adapt_class_name('major_title')}>{major[1]}</div>
        <div className={get_adapt_class_name('minor_title')}>{minor[1]}</div>
      </div>
    </div>
  );
};
