import * as React from 'react';
import { ScrollView } from './components/scrollview';
import { HelpTheatreViews } from './components/theatre';
import { HelpEffectsViews } from './components/special_effects';
import { HelpPenViews } from './components/pen';
import { HelpMidiViews } from './components/midi';
import { HelpImportMusic } from './components/import_music';
import { HelpTheatreType, HelpEffectsType, HelpPenType, HelpAudioType, HelpImportType } from './interfaces';
import { HelpTitle } from './components/common/title';
import { HelpMidiTitle } from './components/midi/midi_title';
import { get_adapt_class_name, get_values, is_ipad, get_body_height, get_cur_type } from './utils';
import './style.scss';

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface IProps {
}

interface IState {
    icon_visible:boolean;
    scroll_down?:boolean;
}

function is_theatre_help(type:string) {
  return get_values(HelpTheatreType).findIndex((t:string) => type === t) >= 0;
}

function is_effects_help(type:string) {
  return get_values(HelpEffectsType).findIndex((t:string) => type === t) >= 0;
}

function is_pen_help(type:string) {
  return get_values(HelpPenType).findIndex((t:string) => type === t) >= 0;
}

function is_import_help(type:string) {
  return get_values(HelpImportType).includes(type);
}

function get_titles(type:string) {
  if (is_theatre_help(type)) {
    return { major: '', minor: 'Coordinate' };
  } else if (is_effects_help(type)) {
    return { major: '', minor: 'Special effects' };
  } else if (is_pen_help(type)) {
    return { major: '', minor: 'Brush color model' };
  } else if (is_import_help(type)) {
    return { major: 'QQ', minor: 'Import music from QQ WeChat' };
  };
  return { major: '', minor: 'Coordinate' };
}

export class HelpRoot extends React.PureComponent<IProps, IState> {
    private scroll_view:ScrollView | null = null;
    private last_scroll_position:number = 0;

    public constructor(props:IProps) {
      super(props);
      this.state = {
        icon_visible: true,
      };
    }

    private set_ref = (ref:any) => this.scroll_view = ref;

    private get_container_style = ():React.CSSProperties => {
      if (is_ipad()) {
        return { paddingTop: 86, paddingBottom: 24 };
      }
      return { paddingTop: 56, paddingBottom: 16 };
    }

    private on_scroll = () => {
      if (this.state.icon_visible) {
        this.setState({ icon_visible: false });
      }
      if (get_cur_type() === HelpAudioType.Midi && this.scroll_view) {
        const position = (is_ipad() ? 514 : get_body_height()) * 4.5;
        const top = this.scroll_view.get_top();
        if (top >= position && this.last_scroll_position < position) {
          this.setState({ scroll_down: true });
        } else if (top <= position && this.last_scroll_position > position) {
          this.setState({ scroll_down: false });
        }
        this.last_scroll_position = top;
      }
    }

    private on_next_page = () => {
      if (this.scroll_view) {
        const offset = is_ipad() ? 514 : get_body_height();
        this.scroll_view.scroll_to(offset);
      }
    }

    public render() {
      const { icon_visible } = this.state;
      const cur_type = get_cur_type() || HelpTheatreType.Coordinate;
      const show_theatre = is_theatre_help(cur_type);
      const show_effects = is_effects_help(cur_type);
      const show_pen = is_pen_help(cur_type);
      const show_import_help = is_import_help(cur_type);
      const is_midi = cur_type === HelpAudioType.Midi;
      const { major, minor } = get_titles(cur_type);

      return (
        <React.Fragment>
          {is_midi ? (
            <HelpMidiTitle
              scroll_down={this.state.scroll_down}
              major={['MIDI', 'MIDI']}
              minor={['MIDI editor', 'MIDI music']} />
          ) : (
            <HelpTitle
              major={major}
              minor={minor} />
          )}
          <ScrollView
            ref={this.set_ref}
            on_scroll={this.on_scroll}
            classname='help_scroll_view'
            content_classname='help_content'
            container_style={this.get_container_style()}
          >
            {show_theatre && <HelpTheatreViews cur_type={cur_type as any} />}
            {show_effects && <HelpEffectsViews cur_type={cur_type as any} />}
            {show_pen && <HelpPenViews cur_type={cur_type as any} />}
            {is_midi && <HelpMidiViews />}
            {show_import_help && <HelpImportMusic />}
          </ScrollView>
          {icon_visible && !show_import_help && <img
            className={get_adapt_class_name('help_next_page')}
            onClick={this.on_next_page}
            src={is_midi ? require('../assets/webview_res/help_midi_arrow.gif') : require('../assets/webview_res/help_next_page.gif')}
          />}
        </React.Fragment >
      );
    }
}
