import * as React from 'react';
import { static_cfg } from 'nemo/config';
import { get_adapt_video_size } from '../../../utils';
import './index.scss';

interface IProps {
    width:number;
    height:number;
    className?:string;
    src:string;
}

interface IState {
  can_play:boolean;
}

export class HelpVideo extends React.PureComponent<IProps, IState> {
    private ref:HTMLVideoElement | null = null;
    private intersction_observer:IntersectionObserver | undefined;

    private set_ref = (ref:any) => this.ref = ref;

    constructor(props:IProps) {
      super(props);

      this.state = {
        can_play: false,
      };
    }

    public componentDidMount() {
      if (this.ref) {
        this.intersction_observer = new IntersectionObserver(this.play.bind(this));
        this.intersction_observer && this.intersction_observer.observe(this.ref);
      }
    }

    public componentWillUnmount() {
      if (this.intersction_observer && this.ref) {
        this.intersction_observer.unobserve(this.ref);
      }
    }

    private handle_can_play = () => {
      this.setState({
        can_play: true,
      });
    }

    private play(entries:IntersectionObserverEntry[]) {
      if (this.ref) {
        const entry = entries[0];
        if (entry) {
          if (entry.intersectionRatio > 0) {
            this.ref.play();
          } else {
            this.ref.pause();
          }
        }
      }
    }

    public render() {
      const { width, height, className, src } = this.props;
      const { can_play } = this.state;
      const size = get_adapt_video_size(width, height);
      const src_url = `${static_cfg().qn_path}${static_cfg().qn_key_prefix}help_video/${src}.mp4`;
      return (
        <div className='common_video_content'>
          {!can_play && (
            <div
              className='loading_bg'
              style={{ width, height }}
            >
              <span className='loading_icon'/>
            </div>
          )}
          <video
            onCanPlay={this.handle_can_play}
            ref={this.set_ref}
            width={size.width}
            height={size.height}
            className={`${can_play ? 'video_show' : 'video_hidden'} ${className}`}
            loop
            playsInline
            muted
            autoPlay
            webkit-playsinline='true'
          >
            <source
              src={src_url}
              type='video/mp4' />
                  video.
          </video>
        </div>
      );
    }
}
