import * as React from 'react';
import { get_body_height, is_ipad, get_adapt_class_name } from '../../../utils';
import './style.scss';

interface IProps extends React.Props<any> {
    className?:string;
    first?:boolean;
}

export const HelpWrap:React.FunctionComponent<IProps> = (props) => {
  const { children, className } = props;
  let classes = get_adapt_class_name('help_wrap');
  classes += className ? ` ${className}` : '';
  return (
    <div
      className={classes}
      style={is_ipad() ? {} : { height: get_body_height() }} >
      {children}
    </div>
  );
};
